/* Links */
.ama-theme {
  a {
    @extend %text-transition;
    color: $black;
    text-decoration: none;
    outline: 0;
    &:hover,
    &:focus {
      color: $black;
    }
  }

  .link-black {
    &:link,
    &:visited {
      color: $black;
    }
    // if the default link style changes this will need to include color: black; text-decoration: none;
    &:active,
    &:hover,
    &:focus {
      color: $blue;
      text-decoration: none;
    }
  }

  .link-white {
    &:link,
    &:visited {
      color: $white;
    }
    // if the default link style changes this will need to include color: black; text-decoration: none;
    &:active,
    &:hover,
    &:focus {
      color: $white;
      text-decoration: underline;
    }
  }

  .link-blue {
    font-weight: $font-weight-bold;

    &:link,
    &:visited {
      color: $blue;
    }
    // if the default link style changes this will need to include color: black; text-decoration: none;
    &:active,
    &:hover,
    &:focus {
      color: $blue;
      text-decoration: underline;
    }
  }
} //closes .ama-theme