.section-header {
  margin-top: 50px;
  margin-bottom: 40px;
  padding-bottom: 30px;
  text-align: center;
  border-bottom: 1px solid $gray-8;
  width: 100%; //for IE11

  @include breakpoint($bp-small) {
    margin-top: 60px;
    margin-bottom: 50px;
    padding-bottom: 40px;
  }
}

.section-header_subtitle {
  @extend %type-large;
  margin-top: 12px;

  @include breakpoint($bp-small) {
    margin-top: 18px;
  }
}

