.ama-theme {
  /* Blockquote */
  blockquote {
    font-style: italic;
    border-left: 1px solid $gray;
    color: $gray;
    padding-left: $pad;
    margin-bottom: $space;
  }

  /* Horizontal Rule */
  hr {
    border: 0;
    height: 2px;
    background: $gray-light-2;
    margin: $space-double 0;
  }

  abbr {
    border-bottom: 1px dotted $gray;
    cursor: help;
  }

  /* Text-Related Elements */
  p {
    margin-bottom: $space;
  }

  // @todo update with semantic class
  .b1,
  %b1 {
    font-size: 1.5em;
    line-height: 1.3;
  }

  // @todo update with semantic class
  .b2,
  %b2 {
    font-size: 1.333em;
    line-height: 1.29;
  }

  // @todo update with semantic class
  .b4,
  %b4 {
    font-size: 1em;
    line-height: 1.3888;
  }

  // @todo update with semantic class
  .b5,
  %b5 {
    font-size: 0.875em;
    line-height: 1.3888;
  }
} // closes .ama-theme