.ama-theme {
  .related-list-title {
    @include type($font-sans-serif, 1.27em, $font-weight-medium, 1.3);
    padding: 30px 0;
    border-top: 1px solid $gray-8;
    color: $purple;
    text-align: center;

    @include breakpoint($bp-small) {
      @include type($font-sans-serif, 1.5em, $font-weight-medium, 1.25);
      padding: 40px 0;
    }
  }
} // closes .ama-theme