/*------------------------------------*\
    $Table
\*------------------------------------*/
.ama-theme {
	table {
		border-collapse: collapse; 
		border-spacing: 0; 
		border: 1px solid $gray;
		width: 100%;
	}
	th {
		text-align: left;
		border: 1px solid $gray;
		padding: 0.2em;
	}
	td {
		border: 1px solid $gray;
		padding: 0.2em;
	}
}