/*------------------------------------*\
    $MEDIA ELEMENTS
\*------------------------------------*/

/* Flexible Media */
.ama-theme {
  img,
  video,
  object {
  	max-width: 100%;
  	height: auto;
  }

  iframe {
  	margin-bottom: $space;
  }

  figure {
  	margin-bottom: $space;
  	img {
  		margin-bottom: $space-half;
  	}
  }
  figcaption {
  	font-style: italic;
  }
}