.ama-theme {
  .hub-header {
    padding: 0 25px;
    background-color: $purple;

      @include breakpoint($bp-med) {
        position: fixed;
        width: 100%;
        top: 0;
        z-index: 100;

        &+ .container,
        &+ article {
          margin-top: 87px;
      }
    }
  }

  .hub-header-inner {
    @extend %clearfix;
    display: flex;
    margin: 0 auto;
    max-width: 1200px;
    padding: 5px 0;
    align-items: center;

    @include breakpoint($bp-med-home) {
      padding: 8px 0;
    }

    .logo {
      display: inline-block;
      width: 120px;
      height: 65px;
      order: 1;

      @include breakpoint($bp-med) {
        width: 163px;
        height: 65px;
      }
    }
  }

  .hub-header_messaging {
    display: flex;
    align-items: center;
    flex-grow: 2;
    flex-wrap: wrap;
    justify-content: center;
    margin-left: 20px;

    @include breakpoint(1000px) {
      flex-wrap: nowrap;
    }

    .button-secondary {
      @extend %type-small;
      order: 2;
      padding: 7px 20px;
      margin-left: auto;

      @include breakpoint($bp-small) {
        max-width: 200px;
        position: relative;
        top: 10px;
      }

      @include breakpoint(1000px) {
        width: auto;
        position: static;
        top: auto;
      }
    }
  }

  .hub-header_title {
    @include type($font-sans-serif, 23px, $font-weight-thin, 1.3043478261);
    color: $white;
    order: 1;
    display: none;
    margin-left: auto;
    text-align: center;
    margin-bottom: 10px;

    @include breakpoint($bp-small) {
      display: block;
      width: calc(100% - 200px);
    }

    @include breakpoint(1000px) {
      @include type($font-sans-serif, 28px, $font-weight-thin, 1.25);
      margin-bottom: 0;
      width: auto;
    }
  }

  .hub-header_small {
    @extend %type-smaller;
    margin-left: auto;
    color: $white;
    order: 1;


    .hub-header_cta {
      display: none;
    }

    @include breakpoint($bp-small) {
      order: 3;
      min-width: 200px;
      margin-left: 0;
    }

    @include breakpoint(1000px) {
      margin-left: 20px;
      max-width: 250px;
      text-align: right;
      min-width: unset;

      .hub-header_cta {
        display: inline-block;
      }
    }
  }

  .hub-header_text {
    display: block;
    margin-bottom: 0;

    @include breakpoint($bp-small) {
      position: relative;
      top: -10px;
    }

    @include breakpoint(1000px) {
      position: static;
      top: auto;
    }
  }
} // closes .ama-theme