.ama-theme {
  .hub-card,
  .card-cta {
    display: flex;
    flex-direction: column;
    background-color: $black-7;
    color: $black;
    padding-bottom: 12px;
    width: 100%;
    margin-bottom: 20px;
    text-align: center;

    &:hover {
      background-color: $gray-light-4;
      color: $black;
    }

    @include breakpoint($bp-small $bp-med) {
      // 2-up layout
      // when there are three items it's 50:50
      &:nth-child(1):nth-last-child(2) {
        margin-right: 12px;
        width: calc(50% - 12px);

        &.hub-card-portrait,
        &.card-cta-portrait {
          min-height: calc((((100vw -  32px) / 2) / 2) * 3);
        }
      }

      &:nth-child(2):nth-last-child(1) {
        margin-left: 12px;
        width: calc(50% - 12px);

        &.hub-card-portrait,
        &.card-cta-portrait {
          min-height: calc((((100vw -  32px) / 2) / 2) * 3);
        }
      }

      // 3-up layout
      // when there are three items it's 50:50/100
      // first item is 
      &:nth-child(1):nth-last-child(3) {
        margin-right: 12px;
        width: calc(50% - 12px);

        &.hub-card-portrait,
        &.card-cta-portrait {
          min-height: calc((((100vw -  32px) / 2) / 2) * 3);
        }
      }
      // last of three doesn't have right padding
      &:nth-child(2):nth-last-child(2) {
        margin-left: 12px;
        width: calc(50% - 12px);

        &.hub-card-portrait,
        &.card-cta-portrait {
          min-height: calc((((100vw -  32px) / 2) / 2) * 3);
        }
      }

      &:nth-child(3):nth-last-child(1) {
        width: 100%;

        &.hub-card-portrait,
        &.card-cta-portrait {
          // modified so this isn't giant but it results in it being more landscape shape
          min-height: calc((((100vw -  32px) / 2)) * 1.5);
        }
      }

      // 3-up layout
      // when there are three items it's 50:50/50:50
      &:nth-child(1):nth-last-child(4),
      &:nth-child(2):nth-last-child(3),
      &:nth-child(3):nth-last-child(2),
      &:nth-child(4):nth-last-child(1) {
        width: calc(50% - 12px);

        &.hub-card-portrait,
        &.card-cta-portrait {
          min-height: calc((((100vw -  32px) / 2) / 2) * 3);
        }
      }
      // first of three doesn't have left padding
      &:nth-child(1):nth-last-child(4),
      &:nth-child(3):nth-last-child(2) {
        margin-right: 12px;
      }
      // last of three doesn't have right padding
      &:nth-child(2):nth-last-child(3),
      &:nth-child(4):nth-last-child(1) {
        margin-left: 12px;
      }
    }

    @include breakpoint($bp-med) {
      // 2-up layout
      // when there are three items it's 50:50
      &:nth-child(1):nth-last-child(2) {
        margin-right: 12px;
        width: calc(50% - 12px);

        &.hub-card-portrait,
        &.card-cta-portrait {
          min-height: calc((((100vw -  32px) / 2) / 2) * 3);
        }
      }

      &:nth-child(2):nth-last-child(1) {
        margin-left: 12px;
        width: calc(50% - 12px);

        &.hub-card-portrait,
        &.card-cta-portrait {
          min-height: calc((((100vw -  32px) / 2) / 2) * 3);
        }
      }
      
      // 3-up layout
      // when there are three items it's 33:33:33
      &:nth-child(1):nth-last-child(3),
      &:nth-child(2):nth-last-child(2),
      &:nth-child(3):nth-last-child(1) {
        width: calc(33% - 16px);
        margin: 0 14px;

        &.hub-card-portrait,
        &.card-cta-portrait {
          min-height: calc((((100vw -  42px) / 3) / 2) * 3);
        }
      }
      // first of three doesn't have left padding
      &:nth-child(1):nth-last-child(3) {
        margin-left: 0;
      }
      // last of three doesn't have right padding
      &:nth-child(3):nth-last-child(1) {
        margin-right: 0;
      }

      // 4-up layout
      // when there are three items it's 25:25:25:25
      &:nth-child(1):nth-last-child(4),
      &:nth-child(2):nth-last-child(3),
      &:nth-child(3):nth-last-child(2),
      &:nth-child(4):nth-last-child(1) {
        width: calc(25% - 18px);
        margin: 0 12px;

        &.hub-card-portrait,
        &.card-cta-portrait {
          min-height: calc((((100vw -  42px) / 4) / 2) * 3);
        }
      }
      // first of three doesn't have left padding
      &:nth-child(1):nth-last-child(4) {
        margin-left: 0;
      }
      // last of three doesn't have right padding
      &:nth-child(4):nth-last-child(1) {
        margin-right: 0;
      }
    }

    // when max width is reached
    @include breakpoint($bp-large) {
      // 3-up layout
      // when there are three items it's 33:33:33
      &:nth-child(1):nth-last-child(3),
      &:nth-child(2):nth-last-child(2),
      &:nth-child(3):nth-last-child(1) {

        &.hub-card-portrait,
        &.card-cta-portrait {
          min-height: 570px;
        }
      }

      // 4-up layout
      // when there are three items it's 25:25:25:25
      &:nth-child(1):nth-last-child(4),
      &:nth-child(2):nth-last-child(3),
      &:nth-child(3):nth-last-child(2),
      &:nth-child(4):nth-last-child(1) {
        &.hub-card-portrait,
        &.card-cta-portrait {
          min-height: 423px;
        }
      }
    }
  }

  .hub-card_image,
  .card-cta_image {
    width: 100%;
    order: 1;
    min-height: 100px;
  }

  .hub-card_title,
  .card-cta_title {
    @extend %type-larger;
    padding: 24px 0;
    margin: 0 24px;
    border-bottom: 1px solid $black;
    order: 2;
  }

  .hub-card_image + .hub-card_title,
  .card-cta_image + .card-cta_title {
    border-bottom: 0;
    order: 3;
  }

  .hub-card_image + .hub-card_title {
    margin-top: auto;
  }

  .hub-card_desc,
  .card-cta_desc {
    @include type($font-sans-serif, 18px, $font-weight-medium, 1.3888888889);
    padding: 24px 0 12px;
    margin: 0 24px;
    order: 2;

    @include breakpoint($bp-med) {
      @include type($font-sans-serif, 20px, $font-weight-medium, 1.3333333333);
    }
  }

  .hub-card-portrait,
  .card-cta-portrait {
    // need to do larger widths depending on the number of children
    min-height: calc(((100vw -  40px) / 2) * 3);
    justify-content: flex-end;
    background-size: cover;
    background-position: center;

    .button {
      margin: 0 24px 12px;
    }

    // when the portrait is hovered, trigger the 'button' hoverstate
    &:active,
    &:hover,
    &:focus {

      .button-secondary {
        border-color: $purple-darker;
        background-color: #e3dce9;
        color: $purple-darker;
      }
    }
  }

  /* Hub card with select - EWL-4133 */
  .hub-card-with-select:hover {
    background: $black-7;
  }

  .hub-card select {
    width: auto;
    order: 3;
    margin: 1em 1em 0 1em;
  }

} // closes .ama-theme