.ama-theme {
  .list-events {
    margin: 0;
    padding: 0;
  }

  .list-events_item {
    border-top: 1px solid $gray-8;
    list-style-type: none;
    margin-bottom: 0;
    padding: 30px 0;
    @include breakpoint($bp-small) {
      padding: 40px 0;
    }
  }

  .list-events_item_summary,
  .list-events_item_date {
    padding-bottom: 9px;
  }

  .list-events_item_summary,
  .list-events_item_date,
  .list-events_item_location {
    @include grid__unit--cols(12);
    display: block;
    vertical-align: text-top;
  }

  .list-events_item_date,
  .list-events_item_location {
    font-size: 0.9em;
  }

  .list-events_item p {
    margin-bottom: 0;
  }

  .list-events_item_summary a {
    display: inline-block;
    font-size: 24px;
    padding-bottom: 9px;

    .icon-external {
      height: 9px;
      width: 9px;
      margin-left: 7px;
    }

    &:hover .external {
      fill: $blue; 
    }
  }

  .list-events_item_summary_title-sold-out:after {
    content: "Sold Out";  // For display purposes only--this would be generated dynamically.
    color: $red;
    margin-left: 10px;
    display: inline-block;
  }

  .list-events_item_date_day {
    &:after {
      content: " / ";
    }
  }

  .list-events_item_date_day,
  .list-events_item_date_time {
    display: inline-block;
  }

  @include breakpoint($bp-small) {
    .list-events_item_summary {
      @include grid__unit--cols(6);
      display: inline-block;
      a {
        .icon-external {
          height: 10px;
          width: 10px;
        }
      }
    }

    .list-events_item_summary,
    .list-events_item_date {
      padding-bottom: 0;
    }

    .list-events_item_date,
    .list-events_item_location {
      @include grid__unit--cols(2);
      display: inline-block;
      margin-left: 8.3%;
    }

    .list-events_item_date_day,
    .list-events_item_date_time {
      display: block;
    }

    .list-events_item_date_day {
      &:after {
        content: none;
      }
    }
  }
} // closes .ama-theme
