.ama-theme {
  .search_modal {
    display: none;
    @extend %clearfix;
    margin: 0 auto;
    max-width: 1200px;
    position: relative;

    &.is-open {
      display: block;
    }
  }

  .search_modal_inner {
    @include grid__unit--cols(12);
    flex-direction: row;
    justify-content: flex-start;
    width: 100vw;
    position: absolute;
    left: 0;
    right: 0;
    background-color: $black-7;
    padding-bottom: 50px;
    z-index: 10;

    .grid {
      position: relative;
    }
  }

  .search_modal_close {
    position: absolute;
    top: 40px;
    right: 3px;
    width: 20px;
    height: 20px;
    color: transparent;
    z-index: 1;
    cursor: pointer;

    svg {
      fill: $blue;
    }

    &:active,
    &:hover,
    &:focus {
      color: transparent;
    }
  }

  .search_modal_search-field {
    padding-left: 0;
    padding-right: 0;
  }
} // closes .ama-theme
