.ama-theme {
  // The tools block.
  .topic_tools {
    max-width: 800px;

    ul li {
      margin-bottom: 0; // override default list styles
    }

    // Tools links.
    .tools_list-learn-more {
      list-style-type: disc;
      margin-left: 2em;
      @include gutter($margin-top-half...);

      .list_links_item {
        @extend %type-smaller;
      }
    }
  }

  .tools_title,
  .tools_title-learn-more {
    margin-bottom: 0;

    @include breakpoint($bp-med) {
      margin-left: 0;
    }
  }

  .tools_title-learn-more {
    @include gutter($margin-top-half...);
  }

} // closes .ama-theme